
<template>
  <div class="pwd">
    <van-nav-bar
    fixed
    title="修改密码"
    left-arrow
    @click-left="onClickLeft">
    </van-nav-bar>
    <div class="pwd-main">
      <van-cell-group :border="false" class="login-container-form">
        <p>原密码</p>
        <van-field
          v-model="oldPwd"
          clearable
          placeholder="请输入原密码">
        </van-field>
        <p>新密码</p>
        <van-field
          v-model="newPwd"
          clearable
          placeholder="请输入新密码">
        </van-field>
        <p>确定新密码</p>
        <van-field
          v-model="confirmPwd"
          clearable
          placeholder="请再次输入新密码">
        </van-field>
      </van-cell-group>
      <div class="pwd-rule">
        <div>密码规则:</div>
        <div>1.密码长度为8~32位字符</div>
        <div>2.必须包含大小写字母和数字</div>
        <div>3.请勿连续包含三个及以上相同字符</div>
      </div>
      <van-button type="info" class="pwd-btn">确定</van-button>
    </div>
  </div>
</template>

<script>
import { NavBar, Field, CellGroup, Button } from 'vant'

export default {
  components: {
    [NavBar.name]: NavBar,
    [Field.name]: Field,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup
  },
  data () {
    return {
      oldPwd: '',
      newPwd: '',
      confirmPwd: ''
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/me/index',
        query: {}
      })
    }
  }
}
</script>
<style scoped>
.pwd{
  font-size: 14px;
  padding-top: 46px;
  color: #333;
}
.pwd-main{
  margin: 30px auto;
  width: 80%;
}
.pwd-btn{
  width: 260px;
  height: 30px;
  line-height: 30px;
  display: block;
  margin: 0 auto;
}
.pwd-rule{
  color: #999;
  font-size: 12px;
  padding: 20px 0;
}
</style>



